body {
  margin: 0;
  font-family: "Neue Montreal";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: 'Montreal';
  src: url(font/NeueMontreal-Regular.otf);
  font-style: normal;
  font-weight: 100;

}
@font-face {
  font-family: 'Montreal';
  src: url(font/NeueMontreal-RegularItalic.otf);
  font-style: italic;
  font-weight: 100;
}

.trails-main {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(90deg);
}

.trails-text {
  position: relative;
  width: 100%;
  height: 70px;
  line-height:70px;
  color: black;
  font-size: 4em;
  font-weight: 100;
  text-transform: uppercase;
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo {
  display: block;
  margin: auto;
  width: 400px;
  padding-top: 1rem;
}
.flex-container {
    display: flex;
    flex-direction: column;

}
.link {
  text-decoration: none;
  color: black;
}
.link:hover{
  font-style: italic;
}
.pegaso {
  position: absolute;
  top: 300px;
  width: 600px;
  left: 650px;

}

.titulos {
  font-size: 55px;
  font-family: 'Montreal';
  font-weight: 100;
  letter-spacing: 1px;
}
.tituloin{
  font-size: 30px;
  font-family: 'Montreal';
  font-style: italic;
  font-weight: 100;

}
.textos {
  font-size: 20px;
  font-family: 'Montreal';
  font-weight: 100;
}
.container{
  text-align: right;
}
.boton{
  padding-right: 100%;
}

.contdig {
  position: absolute;
  top: 360px;
  left: 1175px;
  width: 300px;
  height: 200px;

  
  transform: rotate(90deg);
}
.cons {
  position: absolute;
  left: 650px;
  top: 190px;

}
.res {
  position: absolute;
  top: 380px;
  left: 410px;
  transform: rotate(270deg);
}
.rev {
  position: absolute;
  top: 630px;
  left: 650px;
}
.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  font-family: 'Montreal';
  font-weight: 100;
  font-size: 14px;
  background-color: white;
  padding-bottom: 2rem;
  text-align: left;
}
table{
  width: 80%;
}
td { 
  width: 33%;
}
.direc {
  float: left;
  padding-left: 2rem;
}
.contac {
  display: inline-block;
  padding-left: 10rem;
  
}
.inst {
  float: right;
  padding-right: 2rem;
}
a {
  text-decoration: none;
  color: black;
}
a:hover{
  font-style: italic;
}
